.Card{
    width: 80vw;
    height: 40vh;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}
.Card img{
    z-index: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: height 1s;

}

.Card:hover > img{
    height: 0%;
}

.Card-info{

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;


    font-weight: bold;
    color:rgb(5, 5, 5);
    position: absolute;
    transition-delay: 0s;
}   
.Card-title{
    font-size: 2rem;
    margin-bottom: 6px;
}
.Card-subtitle{
    font-size: 2rem;
    color: #c31aa3;
}
.Card:hover card{
    margin-top: 0;
    
}
.Card-button{

    transition: margin-top 1s;
}

.Card svg{
    margin: 1rem;
    font-size: 3em;
}
.Card svg:hover{
    cursor: pointer;
    color: #c31aa3;
}
.Card:hover .Card-info-top{
    margin-bottom: 0%;

}
.Card .Card-info-top{
    margin-bottom: 100%;

    transition: margin-bottom 1s;

}
@media only screen and (min-width: 1110px) {

    .Card{
        width: 30vw;

    }
  }