.profileImg{
    border-radius: 40px;
    width: 293px;
    width: 100%;
    max-width: 300px;
    height: auto;
    margin-bottom: 2rem;
}
.About-page{
    overflow: hidden;
}
.About-section{
    margin-bottom: 2rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.About-s-body{
    margin-top: 3rem;
    justify-content: center;
    flex: 4;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}
.About-s-footer{
    margin-top: 1rem;
    align-items: center;
    justify-content: center;
    flex: 2;
    display: flex;
    place-items: flex-start;
}
.About-me{
    font-size: 180%;
    width: 81%;
}
.About-section-2{
    grid-gap: 3em;
    max-height: 0;
    grid-template-columns: repeat(1, 1fr);
    justify-content: center;
    align-items: center;
    flex-direction: column;
    display: grid;
    font-size: 1.5rem;
    -webkit-transition: max-height 1s  ease-in-out;
    transition: max-height 1s ease-in-out;
}
.About-section-2 > div{
    text-align: center;
}
.About-section-2 svg{
    font-size: 4rem;
}
.About-grid{
    flex: 1;
    height: 36vh;
}
.About-Tech{
    grid-template-columns: repeat(3, 1fr);
    display: grid;
    grid-gap: 3rem 0rem;
}
.About-ss{
    flex: 1;
}
.About-st{
    flex: 2;
}
.About-services{
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    display: flex;
    font-size: 1.3rem;
    grid-gap: 1rem 1rem;

}
.About-services div {
    width: 42%;
}
.About-Button-More{
    top: 21rem;
    background-color: #ba02a3;
    border: 1px solid rgba(255, 255, 255, 0);
    border-radius: 9px;

}
.About-Button-More:focus{
    outline: none;
}
.About-Button-More:hover{
    cursor: pointer;

}
.About-Button-More svg{
    font-size: 3rem;
}
.About-Button-More > .ArrowMoreIcon{
    animation: arrow 1.4s ease infinite;

}

.ArrowMoreIcon{
    display: inline-block;
    color: white;
    transform:translateY(0px);
    transition: transform 0.5s;
    padding: 0.3rem;
}
.About{
    min-height: 100%;
    overflow:hidden;
    height: auto;
    

}

.About.open > .About-section-2{
    max-height: 200vh;

}
.container .about-div{
    background: white;
    height: 100vh;
    width: 100%;
}
@keyframes arrow {
    0% {
        transform:translateY(0px);
    }
    50% {
        transform:translateY(10px);
    }
    100% {
        transform:translateY(0px);
    }
}

/* responsive */
@media only screen and (min-width: 1110px) {

    .About-section-2{
        place-items: center;
        grid-gap: 6rem;
    }
    .About-me{
        font-size: 1.7rem;
        width: 40%;
    }
    .About-st{
        align-self: center;
        width: 26vw;
    }
    .About-section{   
         height: 100vh;

    }

    .profileImg{
        margin-right: 2rem;
    }
    .About-services div {
        width: 23%;
    }
  }